<template>
  <div v-if="loaded">
    <div v-if="isAuthenticated">
      <div class="title">Система ведения кадастровой инфомации</div>
      <div class="menu_container">
        <div class="menu">
          <div id="nav">
            <img
              src="@/assets/micromine.png"
              alt="Micromine"
              width="150"
              id="logo_top"
            />
            <!-- MainWindow / -->
            <router-link to="/dicts">Общие справочники</router-link> |
            <router-link to="/districts">Справочники районов</router-link> |
            <router-link to="/deposits">Месторождения</router-link> |
            <!-- router-link to="/transfer">Мастер переноса</router-link> | -->
            <!-- router-link to="/protocols">Протоколы</router-link> | -->
            <router-link to="/reports">Отчеты</router-link> |
            <router-link to="/security">Безопасность</router-link> |
            <router-link to="/about">О программе</router-link> |
            <!-- router-link to="/configuration">Настройки</router-link> | -->
            <a href="#" @click="logout()">Выход</a>
          </div>
        </div>
      </div>
      <router-view></router-view>
    </div>
    <div v-if="!isAuthenticated">
      <Login />
      <img src="@/assets/micromine.png" alt="Micromine" width="150" id="logo" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Login from "@/views/Login.vue";

export default {
  name: "App",
  data() {
    return {
      isAuthenticated: false,
      loaded: false,
    };
  },
  methods: {
    checkAuth() {
      const token = sessionStorage.getItem("token");
      const headers = {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      };
      const url = this.$BASE_URL + "/auth/validate_token/";
      console.log(url);
      axios.post(url, {}, { headers }).then((response) => {
        this.loaded = true;
        if (response.data[0].valid) {
          this.isAuthenticated = true;
          //this.$router.push("/main/");
        } else {
          this.isAuthenticated = false;
        }
      });
    },
    logout() {
      sessionStorage.setItem("token", null);
      this.isAuthenticated = false;
      this.$router.push("/");
    },
    pollAuthData() {
      this.polling = setInterval(() => {
        this.checkAuth();
      }, 60000);
    },
  },
  mounted() {
    this.checkAuth();
    this.pollAuthData();
    this.$router.push("/");
  },
  components: {
    Login,
  },
};
</script>

